.home .banner {
    position: relative;
    background-image: url('../assets/images/home-banner-mob.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100%;
}
.home .banner::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.2;
}
.soon {
    position: fixed;
    bottom: 18px;
    color: #fff;
    font-size: 16px;
    width: 100%;
    text-align: center;
}
.active .clip {
    animation: 250ms draw forwards linear;
}
.active .clip.p {
    animation: 100ms draw forwards linear;
    animation-delay: 3.5s;
}
.clip.j1 {
    stroke-dasharray: 284;
    stroke-dashoffset: 284;
}
.clip.j2 {
    stroke-dasharray: 388;
    stroke-dashoffset: 388;
    animation-delay: 250ms;
}
.clip.a1 {
    stroke-dasharray: 95;
    stroke-dashoffset: 95;
    animation-delay: 500ms;
}
.clip.a2 {
    stroke-dasharray: 124;
    stroke-dashoffset: 124;
    animation-delay: 750ms;
}
.clip.y1 {
    stroke-dasharray: 165;
    stroke-dashoffset: 165;
    animation-delay: 1s;
}
.clip.y2 {
    stroke-dasharray: 202;
    stroke-dashoffset: 202;
    animation-delay: 1.25s;
}
.clip.s1 {
    stroke-dasharray: 90;
    stroke-dashoffset: 90;
    animation-delay: 1.5s;
}
.clip.s2 {
    stroke-dasharray: 128;
    stroke-dashoffset: 128;
    animation-delay: 1.75s;
}
.clip.on1 {
    stroke-dasharray: 94;
    stroke-dashoffset: 94;
    animation-delay: 2s;
}
.clip.on2 {
    stroke-dasharray: 250;
    stroke-dashoffset: 250;
    animation-delay: 2.25s;
}
.clip.f1 {
    stroke-dasharray: 263;
    stroke-dashoffset: 263;
    animation-delay: 2.5s;
}
.clip.f2 {
    stroke-dasharray: 375;
    stroke-dashoffset: 375;
    animation-delay: 2.75s;
}
.clip.f3 {
    stroke-dasharray: 124;
    stroke-dashoffset: 124;
    animation-delay: 3s;
}
.clip.p {
    stroke-dasharray: 409;
    stroke-dashoffset: 409;
}
.content {
    display: flex;
    justify-content: center;
    height: 100vh;
}
.signature {
    position: relative;
    align-self: center;
    font-size: 40px;
    font-family: 'salesforce';
    font-weight: 100;
    color: #fff;
    margin: 40px;
    padding-bottom: 50%;
    transition: 200ms all ease-in-out;
    opacity: 0.8;
}
.signature svg {
    width: 100%;
    height: 100%;
}
@keyframes draw {
    100% {
        stroke-dashoffset: 0;
    }
}

@media only screen and (min-width: 48em) {
    .signature {
        margin: 80px;
        font-size: 64px;
        padding-bottom: 20%;
    }
    .home .banner {
        background-position: center 80%;
    }
}

@media only screen and (min-width: 78rem) {
    .home .banner {
        background-image: url('../assets/images/home-banner.jpg');
        background-position: center;
    }
    .signature {
        padding-bottom: 5%;
    }
}